@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
html {
  height: 100%;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(0deg, rgba(34,193,195,1) 0%, rgba(45,46,253,1) 100%);
  background-repeat: no-repeat;
  height: 100%;
  background-attachment: fixed;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.App {
  text-align: center;
  height: 100vh;
  display: grid;
  grid-gap: .5em;
  grid-template:
  ". header ." 5em
  ". image ." auto
  ". inputs ." 5em
  ". button ." 2em
  "footer footer footer" 3em
  ". album . " auto
  /1fr 6fr 1fr
}


.header {
  grid-area: header;
  font-family: 'Pacifico', cursive;
  color: white;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-size: 2.5rem;
  font-weight: 800;
}

.image {
  grid-area: image;
  display: grid;
  height: 400px;
}

.inputs {
  grid-area: inputs;
  display: grid;
  align-content: center;
}

Input {
  margin: 0.25rem;
  justify-items: center;
}

.button {
  grid-area: button;
}

.album {
  grid-area: album;
  display: flex;
}

.album > a {
  grid-area: album;
  display: flex;
  flex-flow: wrap;
  justify-content: center;
}

.newMeme {
  height: 100px;
  margin: 0.5rem;
  border: .25rem solid turquoise;
}

.footer {
  grid-area: footer;
  background-color: transparent;
}

.footer > a {
  text-decoration: none;
  font-family: 'Pacifico', cursive;
  color: white;
  font-size: 1.2rem;
  margin-bottom: 1rem;
}

.footer > a:hover {
  text-decoration: none;
  font-family: 'Pacifico', cursive;
  color: turquoise;
}

